<template>
    <v-container grid-list-xs fluid>
        <center>
            <v-card max-width="600" outlined class="pb-3">
                <v-toolbar dense color="primary" flat dark>
                    <v-toolbar-title class="title"><v-icon class="mr-2">mdi-list-status</v-icon>TO DO LIST</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                    <v-row>
                        <v-text-field
                            v-model="toInsert.toDo"
                            outlined dense class="mr-3 ml-3"
                            label="Enter To Do"
                            @keyup.enter='insertTodo()'
                        ></v-text-field>

                        
                    </v-row>
                    <center v-if="todos.length == 0"><h2>Enter your To Do</h2></center>

                    <v-container v-else
                            id="scroll-target"
                            style="max-height: 500px"
                            class="overflow-y-auto"
                        >
                        <v-col cols="12"
                            style="height: 400px"
                        >
                            <v-list >
                                <v-list-item :style="todo.isDone ? 'background-color:#E0F2F1' : ''" class="title font-weight-light" v-for="(todo, i) in todos" :key="i">
                                    <v-list-item-icon>
                                        <v-icon v-if="!todo.isDone">mdi-close</v-icon>
                                        <v-icon v-else color="primary">mdi-check</v-icon>
                                    </v-list-item-icon>
                                    <v-list-item-content>
                                        {{todo.toDo}}
                                    </v-list-item-content>
                                    <v-btn @click="finishedTodo(todo) " icon color="primary">
                                        <v-icon v-if="!todo.isDone">mdi-file-check-outline</v-icon>
                                        <v-icon v-else>mdi-file-remove-outline</v-icon>
                                    </v-btn>
                                    <v-btn @click="deleteToDo(i)" icon color="red" :key="i+'b'"><v-icon>mdi-trash-can-outline</v-icon></v-btn>
                                </v-list-item>
                            </v-list>
                        </v-col>
                    </v-container>
                    
                </v-card-text>
            </v-card>
        </center>
    </v-container>
</template>

<script>
export default {
    data(){
        return{
            toInsert: {},
            editedItem: {toDo: '', isDone: false},
            todos: [],
            editedIndex:-1
        }
    },
    methods:{
        async finishedTodo(a){
            let i = this.todos.indexOf(a)
            Object.assign(this.editedItem, a)
            if(a.isDone){
                this.editedItem.isDone = false
            }else{
                this.editedItem.isDone = true
            }
            await Object.assign(this.todos[i], this.editedItem)

            this.editedIndex = -1
            this.editedItem = {}
            this.toInsert = {}
        },
        insertTodo(){
            this.toInsert.isDone = false
            this.todos.push(this.toInsert)
            this.toInsert = {}
        },  
        deleteToDo(i){
            this.todos.splice(i,1)
        },
        loadToDos(){
            this.todos = [{toDo:'test',isDone: false}]
        }
    },
    computed:{
    }
}
</script>